@import url("https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #3a4257;

  background-image: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Prompt", sans-serif;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.text {
  margin: 2rem 0;
  text-align: center;
  color: #ff8177;
}
.form-control {
  display: flex;
  flex-direction: column;
  height: 80vh;
  justify-content: flex-start;
  align-items: center;
  border-radius: 5px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.5) 0px 8px 16px -8px;
  padding: 1.5rem;
  overflow: auto;
  background-color: #3a42579c;
  border: none;
}
.form {
  display: flex;

  width: 100%;
}
.input-form {
  display: flex;
  flex-direction: row;

  justify-content: space-evenly;
  padding: 0 3rem;
}
.btn-hover {
  width: 50%;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 5px;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  padding: 0 1rem;
  height: 50px;
}
.btn-hover:hover {
  background-position: 100% 0;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.btn-hover.btn-color {
  background-image: linear-gradient(
    to right,
    #ff8177 0%,
    #ff867a 0%,
    #ff8c7f 21%,
    #f99185 52%,
    #cf556c 78%,
    #b12a5b 100%
  );
  box-shadow: 0 4px 15px 0 rgba(240, 221, 188, 0.628);
}

.input-task {
  border-radius: 5px;
  width: 100%;
  padding: 0 1rem;
  outline: none;
  height: 50px;
  border: none;
  background-color: #3a425764;
  color: #f0ddbc;
  box-shadow: #f0ddbc95 0px 2px 6px 0px;
}
ul {
  display: flex;
  flex-direction: column;
  padding: 1rem 3rem;
  margin-top: 40px;
}

.checked {
  text-decoration: line-through;
  color: #403a60;
}

li {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  border: 1px solid #f0ddbc69;
  border-radius: 5px;
  margin: 1rem 0rem;
  width: 100%;
  padding-left: 5px;
  color: #f0ddbcd7;
  cursor: pointer;
}
li p {
  width: 85%;
}
.task-icons {
  display: flex;
  font-size: 20px;
  cursor: pointer;
  padding: 0.6rem;
  transition: all 0.4s ease-in-out;
  /* width: 15%; */
}
.task-icons:hover {
  font-size: 30px;
  transition: all 0.4s ease-in-out;
}
